import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return{
			dconfirmShow:false,
			area:this.$store.state.area,
			HospitalName:'',
			user: this.$store.state.userInfo,
			HospitalAddress:'',
			FaceConsultationAddressId:this.$route.query.FaceConsultationAddressId || 0,
			hospitalInfo:this.$store.state.hospitalInfo,
			AreaId:0,
			AreaName:null,
			
        }
    },
    created(){
		if(this.FaceConsultationAddressId && !this.area){
			this.pull()
		}
		if(this.hospitalInfo){
			this.HospitalName = this.hospitalInfo.HospitalName
			this.HospitalAddress = this.hospitalInfo.HospitalAddress
		}
		if(this.area){
			this.AreaId = this.area.AreaId
			this.AreaName = this.area.AreaName
		}
    },
    mounted(){
       
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
		goArea(){
			this.hospitalInfo.HospitalName = this.HospitalName
			this.hospitalInfo.HospitalAddress = this.HospitalAddress
			this.$store.dispatch('pushhospitalInfo',this.hospitalInfo)
			this.$router.push('/area')
		},
		pull(){
			this.$get('AppointmentAPI/FaceConsultation/GetFaceConsultationAddress',{
				FaceConsultationAddressId:this.FaceConsultationAddressId,
			}).then(o => {
				this.HospitalName = o.HospitalName
				this.HospitalAddress = o.HospitalAddress
				this.AreaId = o.AreaId
				this.AreaName = o.AreaName
			})
		},
		addressConfirm(){
			if(!this.HospitalName){
				return this.$toast('请填写出诊医院名称')
			}
			if(!this.AreaName){
				return this.$toast('请选择出诊地')
			}
			if(!this.HospitalAddress){
				return this.$toast('请输入详细出诊地址')
			}
			//有ID 是编辑
			let api = ''
			if(this.FaceConsultationAddressId){
				api = 'AppointmentAPI/FaceConsultation/EditFaceConsultationAddress'
			}else{
				api = 'AppointmentAPI/FaceConsultation/AddFaceConsultationAddress'
			}
			this.$post(api,{
				DrId:this.user.Data.User.DoctorId,
				DrName:this.user.Data.User.DoctorName,
				FaceConsultationAddressId:this.FaceConsultationAddressId,//面诊出诊地址id(如果是新增则不需要传，编辑必须传)
				HospitalName:this.HospitalName,//医院名称
				HospitalAddress:this.HospitalAddress,//医院详细地址
				AreaId:	this.AreaId,//省市id
				AreaName:this.AreaName,//省市名称
			}).then(o => {
				this.$router.push('/faceConsultation/SetUp')
			})
		},
		
    }
}



